.css-bar-xlg>img {
    width: 213px;
    height: 213px;
    margin-left: 5px;
    margin-top: 5px;
    line-height: 30px;
}

.css-bar-lg {
    width: 100px;
    height: 100px;
    font-size: 20px;
    position: relative;
}

.css-bar-lg:after,
.css-bar-lg>img {
    width: 90px;
    height: 90px;
    margin-left: 5px;
    margin-top: 5px;
    line-height: 90px;
}

.css-bar-lg>img {
    width: 70px;
    height: 70px;
    margin-left: 15px;
    margin-top: 15px;
    line-height: 70px;
}

.css-bar>i {
    width: 70px;
    height: 70px;
    background: #fff;
    line-height: 70px;
    position: absolute;
    border-radius: 100%;
    text-align: center;
    margin-left: 5px;
    margin-top: 5px;
    left: 0px;
    right: 0px;
    z-index: 2;
    font-size: 27px;
    color: #a6b7bf;
}

