.custon__add__btns__class{
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  padding: 3px 5px;
  background: transparent;
  border: 0px;
  box-shadow: 0px 0px 0px transparent !important;
}
.custon__add__btns__class:hover{
  background-color: transparent !important;
    border-color: transparent !important;
}
.custon__add__btns__class:active{
  background-color: transparent !important;
    border-color: transparent !important;
}
.custon__add__btns__class:focus{
  background-color: transparent !important;
    border-color: transparent !important;
}
:host /deep/ .custon__add__btns__class .mat-button-wrapper{
  padding: 0px 15px;
  color: white !important;
  background: #00c292 !important;
  display: block;
  font-size: 15px;
  border-radius: 3px;
  transition: 0.5s;
}
:host /deep/ .custon__add__btns__class .mat-button-wrapper:hover{
  box-shadow: 0px 0px 10px -4px #000000a6;
}
:host /deep/ .custon__add__btns__class .mat-ripple.mat-button-ripple{
  display: none !important;
  opacity: 0 !important;
}
