/*******************
Megamenu
******************/
.topbar .mega-dropdown {
  position: static;
}
.topbar .mega-dropdown .dropdown-menu {
  padding: 30px;
  width: 100%;
  min-height: 480px;
  overflow: auto;
  transform: translate(0px, 58px) !important;
}

/*******************
Designed dropdown
******************/
.topbar .dropdown-menu {
  padding-top: 0px;
  border: 0px;
  box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
}
.topbar .dropdown-menu .with-arrow {
  position: absolute;
  height: 10px;
  overflow: hidden;
  width: 40px;
  top: -10px;
}
.topbar .dropdown-menu .with-arrow > span {
  background-color: #fff;
  width: 15px;
  height: 15px;
  top: 3px;
  left: 15px;
  position: absolute;
  content: "";
  -moz-border-radius: 6px 0 0 0;
  border-radius: 6px 0 0 0;
  /*box-shadow:-3px -3px 8px rgba(0, 0, 0, 0.1);    */
}
.topbar .dropdown-menu.dropdown-menu-right .with-arrow {
  right: 0px;
}
.topbar .dropdown-menu.dropdown-menu-right .with-arrow > span {
  right: 20px;
  left: auto;
}

/*******************
Topbar Responsive
******************/
@media (max-width: 1023px) {
  .topbar .top-navbar .navbar-collapse.show,
.topbar .top-navbar .navbar-collapse.collapsing {
    display: block;
    width: 100%;
    border-top: 1px solid rgba(120, 130, 140, 0.13);
  }
  .topbar .top-navbar .navbar-nav {
    flex-direction: row;
  }
  .topbar .top-navbar .navbar-nav .dropdown {
    position: relative;
  }
  .topbar .top-navbar .navbar-nav > .nav-item > .nav-link {
    padding: 0 10px;
  }
  .topbar .top-navbar .navbar-header {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .topbar .top-navbar .navbar-nav {
    flex-direction: row;
  }
  .topbar .top-navbar .navbar-nav .dropdown {
    position: static;
  }
  .topbar .top-navbar .dropdown-menu {
    position: absolute;
    width: 100%;
    left: 0 !important;
    transform: translate(0px, 64px) !important;
  }
  .topbar .top-navbar .dropdown-menu .with-arrow {
    display: none;
  }
}
